.LuckyWheel {
  width: 600px;
  max-width: 90%;
  margin: 0 auto;
  position: relative;

  &Arrow {
    position: absolute;
    left: 50%;
    top: 17%;
    width: 50px;
    height: 50px;
    transform: translate(-50%, -50%);
    z-index: 5;
  }

  &Circle {
    position: absolute;
    left: calc(50% + 3px);
    top: calc(50% + 3px);
    width: 50px;
    height: 50px;
    transform: translate(-50%, -50%);
    z-index: 5;
  }

  &Items {
    position: absolute;
    left: 16%;
    top: calc(50% - 1px);
    width: 68%;
    height: 68%;
    background-color: transparent;
    border-radius: 50%;
    list-style: none;
    overflow: hidden;
    border: none;
    outline: none;
    transition-timing-function: ease-out;

    // use {
    //   &:nth-child(odd) {
    //     stroke: #374dff;
    //   }

    //   &:nth-child(even) {
    //     stroke: #fdbf07;
    //   }
    // }
  }

  text {
    cursor: default;

    &::selection {
      background-color: transparent;
    }
  }
}
